@use "../../../styles/typography" as *;
@use "../../../styles/variables" as *;

.home {
  position: relative;
  left: 12%;
  height: 15vh;
  width: 88%;
  border-bottom: 0.3rem solid $color-box-shadow;
  &__header {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    .home__logo {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 6rem;
      width: 6rem;
      margin-top: 1rem;
      &--image {
        height: 4rem;
        width: 4rem;
      }
    }
    .home__text {
      height: 4rem;
      margin-top: 0.5rem;
      &--name {
        font-size: 3rem;
      }
    }
  }
}
